import express from 'express';
import { Joi, celebrate } from 'celebrate';
import login from '../controllers/login_post';
import { baseUrl } from '../baseurl';

const router = express.Router();

const userSchema = {
  body: {
    userId: Joi.string().required(),
    password: Joi.string().required(),
  },
};

/**
 * @swagger
 * /auth/login:
 *   post:
 *     tags:
 *       - Authentication
 *     description: Returns a jwt token if login is successful
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required user credentials"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              userId:
 *                  type: string
 *              password:
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              jwt:
 *                  type: string
 *              refreshToken:
 *                  type: string
 *       400:
 *         description: Returns 400 if userId or password are invalid
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.post(`${baseUrl}/login`, celebrate(userSchema), login);

export default router;
